<template>
  <div class="card">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Fleet Maintenance Schedule</span>
        </b-col>
        <b-col style="text-align: right;">
          <span style="color: grey;">
            <b-badge variant="danger">{{totalUnschedule}}</b-badge>
            Unschedule&nbsp;&nbsp;

            <b-badge variant="magenta">{{totalSchedule}}</b-badge>
            Scheduled&nbsp;&nbsp;
            
            <b-badge variant="primary">{{totalQueuedUp}}</b-badge>
            Queued Up&nbsp;&nbsp;
            
            <b-badge variant="success">{{totalOnProgress}}</b-badge>
            On Progress&nbsp;&nbsp;&nbsp;
          </span>
          <span>
            <ABSButton
              :text="'Repair'"
              classButton="button button--back"
              classIcon="icon-style-1"
              @click="doRepair"
              
            />
            <!-- :disabled="ButtonStatus == null ? true : !ButtonStatus.btnAddHocRepair"
             -->
            <ABSButton
              :text="'View All'"
              classButton="button button--back"
              classIcon="icon-style-1"
              @click="doViewAll"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body" style="height: 300px;">
      <div>
        <ACCFormList
          :prop="propList"
          :title="''"
          @rowClicked="rowClicked"
          ref="ref_list"
          @onRenderData="onRenderData"
        >
          <template slot="no" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.no}}</span>
          </template>
          <template slot="fleet_no" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.fleet_no}}</span>
          </template>
          <template slot="fleet_type" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.fleet_type}}</span>
          </template>
          <template slot="last_maint" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.last_maint}}</span>
          </template>
          <template slot="last_millage" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{isCurrency(data.item.last_millage, 0)}}</span>
          </template>
          <template slot="current_millage" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{isCurrency(data.item.current_millage, 0)}}</span>
          </template>
          <template slot="usage" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{isCurrency(data.item.usage, 0)}}</span>
          </template>
          <template slot="maint_type" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.maint_type}}</span>
          </template>
          <template slot="status" slot-scope="data">
            <span :style="data.item.trigger_by === 'U' && data.item.usage > data.item.usage_tolerance ? 'color: red;' : ''">{{data.item.status}}</span>
          </template>
        </ACCFormList>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      propList: {
        OptionUrl: "/OP/OP_FleetMaintSch",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "' AND schedule_status NOT IN('X', 'C')",
        OrderBy: "",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
      totalUnschedule: '0',
      totalSchedule: '0',
      totalQueuedUp: '0',
      totalOnProgress: '0',
    };
  },
  computed: {
    ButtonStatus() {
      return this.$store.getters.getButtonStatus;
    },
  },
  methods: {
    doRepair() {
      this.$router.push({ name: "OP_Repair" });
    },
    doViewAll() {
      this.$router.push({ name: "OP_FleetMaintenanceSchedule" });
    },
    rowClicked(record, index) {
      let param = record;
      param.isEdit = true;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "OP_FleetMaintenanceScheduleForm" });
    },

    RenderData() {
      this.$refs.ref_list.doGetList("");
    },
    onRenderData(data) {
      if (!data) return;
      if (data.length < 1) return;
      
      if (data[0].total_unsch && data[0].total_unsch !== '') {
        this.totalUnschedule = this.isCurrency(data[0].total_unsch, 0);
      }
      if (data[0].total_sch && data[0].total_sch !== '') {
        this.totalSchedule = this.isCurrency(data[0].total_sch, 0);
      }
      if (data[0].total_queue && data[0].total_queue !== '') {
        this.totalQueuedUp = this.isCurrency(data[0].total_queue, 0);
      }
      if (data[0].total_on_progress && data[0].total_on_progress !== '') {
        this.totalOnProgress = this.isCurrency(data[0].total_on_progress, 0);
      }
    }
  },
  mounted() {
    this.RenderData();
  },
};
</script>